<template>
  <div id="comments" :class="{moon: this.$store.state.isMoon}">
    <!--  -->
    <div class="add-comment">
      <el-input
        v-if="addComment.loginNum === 0"
        :class="{nickname: true, 'name-null': isNameNull}"
        clearable
        type="text"
        placeholder=""
        v-model="addComment.nickName"
        maxlength="10"
        show-word-limit
        @blur="isNameNull = false; isNameRepeat = false"
      >
      </el-input>
      <!--  -->
      <h5 class="has-nickname" v-else>{{defaultNickName}}</h5>
      <span v-show="isNameRepeat" style="font-size: 14px; color: red">
        <i class="el-icon-circle-close"></i>
      </span>
      <br>
      <el-input
        :class="{body: true, 'body-null': isBodyNull}"
        :autosize="{ minRows: 3, maxRows: 5}"
        type="textarea"
        placeholder=""
        v-model="addComment.body"
        maxlength="100"
        show-word-limit
        @blur="isBodyNull = false"
      >
      </el-input>
      <div class="btn-container">
        <el-button @click.native="submitComment" icon="el-icon-s-promotion" circle></el-button>
        <el-button @click.native="cancelSubmit" icon="el-icon-delete-solid" circle></el-button>
      </div>
    </div>
    <!--  -->
    <div class="comments-list" v-for="(item, index) in messages" :key="index">
      <el-card shadow="never">
        <h5>{{item.nickName}}</h5>
        <p>{{item.body}}</p>
        <div class="reply" v-show="item.reply.length">
          <h5></h5>
          <p>{{item.reply}}</p>
        </div>
        <p class="time"><span>{{item.time}}</span></p>
      </el-card>
    </div>
    <div v-show="messages.length === 0" style="text-align: center">
      ~~~
    </div>
    <!--  -->
    <div class="show-more" @click="showMoreMessages" v-if="isMoreBtnShow">
      <i class="el-icon-arrow-down"></i>
      <p></p>
    </div>
  </div>
</template>

<script>
import { formatDate } from '@/common/utils.js'

export default {
  name: 'Comments',
  data() {
    return {
      addComment: {  ///
        nickName: '',
        body: '',
        time: '',
        reply: '', //
        loginNum: 0  //
      },
      defaultNickName: '',  //
      isNameRepeat: false, //
      isNameNull: false,  //
      isBodyNull: false,  //
      isShowMore: false, //
      page: 1,  //110
    }
  },
  props: {
    messages: {  ///
      type: Array
    },
    total: {  ////
      type: Number
    }
  },
  computed: {
    isMoreBtnShow() {  //
      if(this.total > this.messages.length) {
        // this.isShowMore = true
        return true
      }else {
        return false
        // this.isShowMore = false
      }
    },
  },
  methods: {
    hasNickName() {  //
      const name = localStorage.getItem('nickName')
      if(name) {
        this.defaultNickName = name
        this.addComment.nickName = name
        this.addComment.loginNum = 1  //10
      }
    },
    submitComment() {  //
      if(this.addComment.nickName.length === 0) {
        return this.isNameNull = true
      }else if(this.addComment.body.length === 0){
        return this.isBodyNull = true
      }else {
        const myDate = new Date()
        this.addComment.time = formatDate(myDate, 'yyyy-MM-dd hh:mm:ss') //
        var postUrl = ''                       
        let a = window.location.href  //url
        let b = a.substring(a.length-13)  //13
        if(b === 'leavemessages') {  //Comments
          postUrl = 'leaveMessages'
        }else {
          postUrl = `articleComments/${this.$route.params.id}`
        }
        this.$http.post(postUrl, this.addComment).then(res => {
          if(res.data.state === 401) {  //
            this.isNameRepeat = true
            this.isNameNull = true
            return
          }else {
            this.$emit('changeMessages', res.data)  //
            this.isNameRepeat = false
            localStorage.setItem('nickName', this.addComment.nickName)  ///localStorage
            this.hasNickName()
            this.addComment.body = ''
          }
        })
      }
    },
    cancelSubmit() {  //
      const name = localStorage.getItem('nickName')
      if(!name) { //
        this.addComment.nickName = ''
      }
      this.addComment.body = ''
    },
    showMoreMessages() {  //
      this.page++
      this.$emit('showMore', this.page)
    }
  },
  created() {
    // localStorage.removeItem('nickName')
    this.hasNickName()
  },
}
</script>

<style lang="less" scoped>
  @import "~less/moon/comments.less";
  #comments {
    width: 90%;
    margin: 0 auto;
    .add-comment {
      .nickname {
        width: 45%;
        margin: 20px 0 15px 0;
        /deep/ .el-input__inner {  /* /deep/scoped */
          border-color: @borderColor;
        }
      }
      .name-null {  //
        /deep/ .el-input__inner {
          border-color: red;
        }
      }
      h5.has-nickname {
        font-size: 17px;
        border-left: 3px solid rgb(164, 182, 223);
        position: relative;
        top: 5px;
      }
      .body {
        width: 100%;
        /deep/ .el-textarea__inner {
          border-color: @borderColor;
        }
      }
      .body-null {  //
        /deep/ .el-textarea__inner {
          border-color: red;
        }
      }
      .btn-container {
        width: 100%;
        text-align: right;
        .el-button {
          margin: 10px 0 0 25px;
          font-size: 18px;
        }
      }
    }
    .comments-list {
      margin-top: 10px;
      .el-card {
        border-color: @borderColor;
        margin-bottom: 20px;
        h5 {
          box-shadow: -4px 0 0px -1px rgb(164, 182, 223);
          font-size: 17px;
        }
        p {
          font-size: 15px;
          margin: 15px 0 15px 0;
        }
        .reply {
          margin-left: 10%;
          h5 {
            box-shadow: -4px 0 0px -1px rgb(255, 72, 0);
          }
        }
        .time {
          text-align: right;
          margin: 0;
          font-size: 14px;
        }
      }
    }
    .show-more {
      text-align: center;
      i {
        font-size: 25px;
        color: inherit;
      }
      p {
        font-size: 12px;
        color: inherit;
      }
      &:hover {
        cursor: pointer;
        color: rgb(3, 104, 172);
      }
    }
  }
  div.moon {
    .add-comment h5 {
      color: @moonColor
    }
    .show-more {
      color: @moonColor;
    }
  }
</style>